// #region cache the needed dom Elements.
const heroSec = document.querySelector('.hero-sec');
const links = window.document.querySelectorAll('a');
const header = window.document.querySelector('.header');
const headerNav = window.document.querySelector('.header__nav');
const togglerBtn = window.document.querySelector('.header__nav__toggle-btn');
const currentYearElements = window.document.querySelectorAll('.current-year');
const scrollToTopBtn = window.document.querySelector('.scroll-to-top-btn');
const preloader = window.document.querySelector('.preloader');
// #endregion

// #region For  change hide preload after window loading.
window.document.body.style.overflow = 'hidden';

// window.addEventListener('load', () => {
window.addEventListener('DOMContentLoaded', () => {
  // window.setTimeout(() => {
  preloader.classList.add('hidden');
  window.document.body.style.overflow = 'auto';
  // }, 1000);
});
// #endregion

// #region  navbar toggler buttom functionality
togglerBtn.addEventListener('click', () => {
  headerNav.classList.toggle('show');
  window.setTimeout(() => {
    togglerBtn.blur();
  }, 1000);
});
// #endregion

// #region handle smooth scrolling in internal navigation
links.forEach((link) => {
  link.addEventListener('click', (e) => {
    const href = link.getAttribute('href');

    if (href === '#') {
      // Scroll back to top
      e.preventDefault();
      window.scrollTo({ top: 0, behavior: 'smooth' });
    } else if (href.lenght !== 1 && href.startsWith('#')) {
      // Scroll to other internal section
      e.preventDefault();
      const sectionEl = document.querySelector(href);

      // sectionEl.scrollIntoView({ behavior: 'smooth' });
      window.scrollTo({
        top: `${sectionEl.offsetTop}`,
        behavior: 'smooth',
      });
    } else {
      // Scroll to others target
      // do normall behavior
    }

    window.setTimeout(() => {
      link.blur();
    }, 1000);

    // Close nav Linst Layer in case small screen
    if (headerNav.classList.contains('show')) {
      headerNav.classList.remove('show');
    }
  });
});
// #endregion

// #region  Update Current year functionality
const currentYear = new Date().getFullYear();
currentYearElements.forEach((ele) => {
  ele.textContent = currentYear;
});
// #endregion

// #region handle scroll to top button functionality
scrollToTopBtn.addEventListener('click', () => {
  scrollToTopBtn.blur();
  window.scrollTo({ top: 0, behavior: 'smooth' });
});

//  show & hidden  scroll to top button Using : Intersection Observer API
const obs = new IntersectionObserver(
  (entries) => {
    const ent = entries[0];

    if (ent.isIntersecting === false) {
      header.classList.add('sticky');
      scrollToTopBtn.classList.add('show');
    } else {
      header.classList.remove('sticky');
      scrollToTopBtn.classList.remove('show');
    }
  },
  {
    // In the viewport
    root: null,
    threshold: 0,
  }
);
obs.observe(heroSec);
// #endregion
